import React from 'react'

const SearchBox = ({handleSearch}) => {
    return (
        <div className="pa2">
            <input
                className="pa3 ba b--green bg-lightest-blue"
                type="search"
                name="search"
                placeholder="Search Robots"
                onChange={handleSearch}
            />
        </div>
    )
}

export default SearchBox
