import React from 'react'

const Card = ({robot}) => {
    const {id, name, email, phone} = robot
    return (
        <div className="tc bg-light-green dib br3 pa3 ma2 grow bw2 shadow-5">
            <img src={`https://robohash.org/${id}`} alt={name} width="200" height="200"/>
            <div>
                <h2>{name}</h2>
                <p>{email}</p>
                <p>{phone}</p>
            </div>
        </div>
    )
}

export default Card
